<template>
  <div>
    <div class="row">
      <div class="col-md-8">
        <router-view/>
      </div>
      <div class="col-md-4">
        <h4>最新项目列表</h4>
        <hr>
        <router-link v-for="project in projectList" :key="project.id" class="nav-link" :to="'/'+project.type+'/'+project.id">{{project.title}}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      projectList: []
    };
  },
  created() {
    const myList = this.GLOBAL;
    this.projectList = myList.filter(l => l.type === "projects").reverse();
  }
};
</script>

<style scoped>
.nav-link{
  color: #2c3e50;
}
</style>


